<template>
  <v-container v-if="isLoaded">
    <v-row> 選択中: {{ check }} </v-row>
    <v-row class="text-center">
      <v-simple-table dense>
        <thead>
          <tr>
            <th
              class="text-center clickable"
              v-for="(header, index_x) in this.tableHeader"
              :key="index_x"
              @click="table_checked_reverse(index_x)"
            >
              {{ header }}
            </th>
          </tr>
        </thead>

        <tbody>
          <tr v-for="chara in outputNamedList" :key="chara.m_NamedType">
            <td>
              {{ chara.m_FullName }}
            </td>
            <td
              v-for="(chara_formalStatus, index) in chara.formalStatus"
              :key="index"
            >
              {{ chara_formalStatus }}
            </td>
          </tr>
        </tbody>
      </v-simple-table>
    </v-row>
  </v-container>
</template>

<style>
.clickable {
  cursor: pointer;
}
</style>

<script>
const axiosBase = require("axios");
const axios = axiosBase.create({
  baseURL: "https://database.kirafan.cn/database/", // バックエンドB のURL:port を指定する
  headers: {
    "Content-Type": "application/json",
    "X-Requested-With": "XMLHttpRequest",
  },
  responseType: "json",
});

export default {
  name: "Main",

  data: () => ({
    NamedList: [],
    outputNamedList: [],

    checked: [],

    isLoaded: false,

    tableHeader: ["名前", "HP", "ATK", "MAT", "DEF", "MDF", "SPD", "LUK"],

    reverse: false,
  }),

  computed: {
    check() {
      let output = [];

      this.checked.forEach((x) => {
        output.push(this.tableHeader[x]);
        // console.log(this.output);
      });
      
      // output.push("&nbsp;&nbsp;ソート:")
      if (this.reverse) {
        output.push("降順");
      }else{
        output.push("昇順");

      }
      // console.log(this.checked);
      // Object.keys(this.checked).forEach((object) => {
      //   if (this.checked[object]) {
      //     output.push(this.tableHeader[Number(object)]);
      //   }
      // });
      // for (let i = 0; i < this.checked.length; i++) {
      //   if (this.checked[i]) {
      //     output.push(this.tableHeader[i]);
      //   }
      // }

      return output.join(",");
    },
  },

  methods: {
    table_checked_reverse(id) {
      // if (this.checked.length === 2) {
      //   this.reverse = !this.reverse;
      // }
      if (id === 0) {
        this.reverse = !this.reverse;
        if (this.reverse) {
          this.outputNamedList = Array.from(this.NamedList).reverse();
        } else {
          this.outputNamedList = Array.from(this.NamedList);
        }
        this.checked = [];
      } else {
        const vm = this;
        const id_place = vm.checked.findIndex((val) => val === id);

        if (
          this.checked.length === 0 
          ||
          (this.checked.length === 1 && id_place !== -1)
        ) {
          this.reverse = !this.reverse;
        }

        //true => false
        if (id_place === -1) {
          let numOfChecking = this.checked.length;
          if (numOfChecking < 2) {
            vm.checked.push(id);
          }
        } else if (vm.checked.length === 2) {
          vm.checked.splice(id_place, 1);
        }

        this.table_sorting(this.checked);
      }
    },

    table_sorting(checked) {
      const first_checked = checked?.[0];
      const second_checked = checked?.[1];

      if (first_checked) {
        let tmp = Array.from(this.outputNamedList);
        // .sort((a, b) => {
        //   const a_fromStatus = a.formalStatus[first_checked - 1];
        //   const b_fromStatus = b.formalStatus[first_checked - 1];

        //   if (a_fromStatus < b_fromStatus) return -1 * this.reverse_sorting();
        //   if (a_fromStatus > b_fromStatus) return 1 * this.reverse_sorting();
        //   return 0;
        // });

        // if (second_checked) {
        tmp.sort((a, b) => {
          const a_fromStatus1 = a.formalStatus[first_checked - 1]; //6
          const b_fromStatus1 = b.formalStatus[first_checked - 1]; //6

          const a_fromStatus2 = second_checked
            ? a.formalStatus[second_checked - 1]
            : 0; //1
          const b_fromStatus2 = second_checked
            ? b.formalStatus[second_checked - 1]
            : 0; //2

          const a1_a2 = a_fromStatus1 + a_fromStatus2;
          const b1_b2 = b_fromStatus1 + b_fromStatus2;

          if (a1_a2 < b1_b2) return -1 * this.reverse_sorting();
          if (a1_a2 > b1_b2) return 1 * this.reverse_sorting();
          return 0;
        });

        // }
        this.outputNamedList = tmp;
      } else {
        this.outputNamedList = this.NamedList;
      }

      /*
       * 76
       * 61
       * 64
       * 53
       *
       * ↓
       *
       * 76 76
       * 64 64
       * 61 53
       * 53 61
       *
       */
    },

    reverse_sorting() {
      return this.reverse ? -1 : 1;
    },

    outputAsCSV() {
      let output = "";
      // console.log(this.tableHeader);
      output += this.tableHeader.join(",");
      // output += "\n"
      this.NamedList.forEach((chara) => {
        output += "\n";
        output += chara.m_FullName;
        output += ",";

        output += chara.formalStatus.join(",");
      });
      return output;
    },
    CSVDownload(csv) {
      let bom = new Uint8Array([0xef, 0xbb, 0xbf]);
      let blob = new Blob([bom, csv], { type: "text/csv" });
      let imgUrl = URL.createObjectURL(blob);
      let fileName = "評価値.csv";
      console.log(csv);

      let link = document.createElement("a");
      link.href = imgUrl;
      link.download = fileName;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      URL.revokeObjectURL(imgUrl);
    },
  },

  async mounted() {
    this.NamedList = (await axios.get("NamedList.json")).data.map((x) => {
      for (let i = 0; i <= 4; i++) {
        x.formalStatus[i] = x.formalStatus[i] / 10 + 3;
      }
      x.formalStatus[5] += 3;
      x.formalStatus[6] += 31;

      return x;
    });
    this.outputNamedList = this.NamedList;

    this.isLoaded = true;

    this.$emit(
      "CSVDownload",
      (() => {
        this.CSVDownload(this.outputAsCSV());
      }).bind(this)
    );
  },
};

/**
 * 77
 * 77
 * 76
 * 76
 * 67
 *
 *
 */
</script>
